'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { getAiSuggestion } from '@/app/suggest/actions';

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Sparkles } from 'lucide-react';

const suggestionSchema = z.object({
  preferences: z.string().min(10, { message: 'Please describe your preferences in at least 10 characters.' }),
});

type SuggestionValues = z.infer<typeof suggestionSchema>;

interface FigureSuggestionClientProps {
  availableFigures: string;
}

export function FigureSuggestionClient({ availableFigures }: FigureSuggestionClientProps) {
  const [suggestion, setSuggestion] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const { register, handleSubmit, formState: { errors, isSubmitting } } = useForm<SuggestionValues>({
    resolver: zodResolver(suggestionSchema),
  });

  const onSubmit = async (data: SuggestionValues) => {
    setError(null);
    setSuggestion(null);
    try {
      const result = await getAiSuggestion(data.preferences, availableFigures);
      if (result.suggestion) {
        setSuggestion(result.suggestion);
      } else {
        setError(result.error || 'Failed to get a suggestion.');
      }
    } catch (e) {
      setError('An unexpected error occurred.');
    }
  };

  return (
    <div className="w-full max-w-2xl mx-auto">
      <Card>
        <CardHeader>
          <CardTitle className="text-3xl font-headline">AI Figure Advisor</CardTitle>
          <CardDescription>
            Tell us what you like (e.g., "I love villains from Star Wars" or "I'm looking for a cool character from One Piece") and our AI will suggest a figure for you!
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid w-full gap-2">
              <Label htmlFor="preferences">Your Preferences</Label>
              <Textarea
                id="preferences"
                placeholder="Describe your favorite characters, series, or styles..."
                {...register('preferences')}
                className="min-h-[100px]"
              />
              {errors.preferences && <p className="text-sm text-destructive">{errors.preferences.message}</p>}
            </div>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? 'Thinking...' : 'Get Suggestion'}
              <Sparkles className="ml-2 h-4 w-4" />
            </Button>
          </form>

          {suggestion && (
            <Card className="mt-8 bg-primary/10 border-primary/20">
              <CardHeader>
                <CardTitle className="flex items-center gap-2 text-primary">
                  <Sparkles />
                  AI Suggestion
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-lg">{suggestion}</p>
              </CardContent>
            </Card>
          )}

          {error && (
            <div className="mt-8 text-center text-destructive">
              <p>{error}</p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
